@import url(https://fonts.googleapis.com/css2?family=Source+Serif+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Serif+Display&display=swap);
html,
body {
  margin: 0;
  padding: 0;
  background: #000;
}

* {
  box-sizing: border-box;
}

body {
  color: #fff;
  font-family: "Source Serif Pro", serif;
}

.header {
  width: 100%;
  padding: 30px;
  background-color: #222;
}

.header .logo {
  font-size: 38px;
  text-align: left;
  color: #777;
  font-weight: normal;
  padding: 0 10px;
}

.allBlocks {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  width: 100%;
  height: auto;
}

.totalCredits {
  text-align: center;
  font-weight: bold;
  font-size: 12px;
  padding: 10px 0 0;
  color: #777;
}

.totalCost {
  text-align: center;
  font-weight: bold;
  font-size: 12px;
  padding: 2px 0 12px;
  color: #777;
}

.creditNum {
  padding: 5px 0 0;
  font-size: 10px;
  display: block;
}

.className {
  font-size: 12px;
  display: block;
  margin: 5px 0 0;
}

.semesterName {
  font-weight: normal;
  font-size: 22px;
  color: #777;
  text-align: center;
  margin: 12px 0 18px;
}

.semesterName:hover {
  cursor: pointer;
}

.semesterBin {
  background: none !important;
}

.semesterBlock {
  transition: background-color 0.5s;
}

.courseBlock {
  transition: background-color 0.5s;
}

.courseBlock:hover {
  transition: background-color 0.5s;
}

.courseBlock.core {
  background-color: rgb(145, 65, 118);
}

.courseBlock.essentials {
  background-color: #c9a238;
}

.courseBlock.lab {
  background-color: rgb(90, 53, 126);
}

.courseBlock.residential {
  background-color: rgb(62, 109, 68);
}

.courseBlock.elective {
  background-color: rgb(63, 72, 197);
}

.courseBlock.capstone {
  background-color: rgb(102, 34, 34);
}

.course-credit-total {
  font-size: 16px;
  position: absolute;
  bottom: 30px;
  right: 30px;
  color: #444;
  font-weight: bold;
}

.addTermButton {
  font-family: "Source Serif Pro", serif;
  border-radius: 10px;
  border: 0;
  background: #333;
  color: #fff;
}

.addTermButton:hover {
  cursor: pointer;
}

